import styled from 'styled-components';
import { PostContentOrigin } from '../Post/styled';

export const LinkButton = styled.button`
  border: none;
  border: 10px;
  width: 60px;
  height: 40px;
  margin: 5px;
  transition: all 0.5s ease-in-out;
  box-shadow: 0 0 5px gray;

  a {
    h2 {
      font-family: 'Do Hyeon', sans-serif;
      font-size: 20px;
      color: black;
      font-weight: 800;
    }
  }

  .link-inner {
    width: 30px;
    height: 30px;
  }
`;

export const ButtonInline = styled.div``;

export const MarkDownWrapper = styled.div`
  background: #1d1d1d;
  position: fixed;
  padding: 100px;
  width: 100vw;
  height: 100vh;
  overflow-y: scroll;

  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;

  .title {
    font-family: 'Do Hyeon', sans-serif;
    font-size: 80px;
    margin: 20px;
    font-weight: 800;
  }

  .titles {
    .titles-item {
      line-height: 2rem;
      font-size: 18px;
      font-weight: 600;
    }
  }
`;

export const MarkDownPostContent = styled(PostContentOrigin)`
  background: white;
  em {
    visibility: visible;
    line-height: 1.2em;
    font-weight: 1000;
    margin-top: 18px;
    font-size: 20px;
    text-decoration: underline;
  }
  code {
    position: relative;
    visibility: visible;
  }
  del {
    visibility: visible;
    text-decoration: none;
    color: gray;
  }
`;
